    <div class="container">
        <div class="navbar">
            <router-link to="/" class="navbarText">HOME</router-link>
            <router-link to="/contact" class="navbarText active">CONTACT</router-link>
        </div>

        <div class="contact-container">
            <div class="contact-left">
                <h1 class="title">
                    Let's <span class="highlight">Talk</span>
                </h1>
                <p class="subtitle">
                    有任何問題或合作機會，歡迎聯繫我！
                </p>

                <div class="contact-info">
                    <div class="info-item">
                        <img src="../assets/email.png" class="info-icon" />
                        <span>alan5899f@email.com</span>
                    </div>
                    <div class="info-item">
                        <img src="../assets/github.png" class="info-icon" />
                        <span>github.com/alan5899f</span>
                    </div>
                    <div class="info-item">
                        <img src="../assets/linkedIn.png" class="info-icon" />
                        <span>Wei-Lun Chen</span>
                    </div>
                </div>
            </div>

            <div class="contact-right">
                <form class="contact-form" @submit.prevent="submitForm">
                    <div class="form-group">
                        <label>Name</label>
                        <input type="text" v-model="form.name" placeholder="Your name" required />
                    </div>

                    <div class="form-group">
                        <label>Email</label>
                        <input type="email" v-model="form.email" placeholder="Your email" required />
                    </div>

                    <div class="form-group">
                        <label>Message</label>
                        <textarea v-model="form.message" placeholder="Your message..." rows="5" required></textarea>
                    </div>

                    <button type="submit" class="submit-btn">
                        <span v-if="!submitted">Send Message</span>
                        <span v-else>✓ Sent!</span>
                    </button>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
import { contactApi } from '@/api/services'

export default {
    name: 'ContactView',
    data() {
        return {
            form: {
                name: '',
                email: '',
                message: ''
            },
            submitted: false
        }
    },
    methods: {
        async submitForm() {
            // 這裡可以接 API 發送表單
            console.log('Form submitted:', this.form)
            try {
                await contactApi.addContact(this.form)
                this.success = true
                this.form = { name: '', email: '', message: '' }
                setTimeout(() => {
                    this.submitted = false
                    this.form = { name: '', email: '', message: '' }
                }, 3000)
            } catch (err) {
                this.error = err.response?.data?.msg || '發送失敗，請稍後再試'
            } finally {
                this.loading = false
            }
        }
    }
}
</script>

