<template>
  <div class="container">
   <div class="navbar">
  <router-link to="/" class="navbarText">HOME</router-link>
  <router-link to="/contact" class="navbarText">CONTACT</router-link>
</div>
    <div class="user-container">
      <img class="userImage" src="../assets/profile.png" />
      <div class="userDescription">
        <div class="text-container">
          <a class="text-line" style="font-size: 32px; font-weight: 400;">
            {{ text1 }}<span class="cursor" v-if="currentLine === 0">|</span>
          </a>
          <a class="text-line" style="font-size: 72px; font-weight: bolder;">
            <span v-if="text2.includes('Meow Chen')">
              I'm <span style="color: #82FDFF">{{ text2.replace("I'm ", "") }}</span>
            </span>
            <span v-else>{{ text2 }}</span>
            <span class="cursor" v-if="currentLine === 1">|</span>
          </a>
          <a class="text-line" style="font-size: 30px; font-weight: 400; padding-top: 50px;">
            {{ text3 }}<span class="cursor" v-if="currentLine === 2">|</span>
          </a>
        </div>
        <div class="icon-container" :class="{ 'show': showIcons }">
          <img class="icon" src="../assets/linkedIn.png" @click="openUrl('https://www.linkedin.com/in/wei-lun-chen-604433249')"/>
          <img class="icon" src="../assets/github.png" @click="openUrl('https://github.com/alan5899f')"/>
          <img class="icon" src="../assets/email.png" @click="openEmail('alan5899f@email.com')"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ProfileView',
  data() {
    return {
      text1: '',
      text2: '',
      text3: '',
      currentLine: 0,
      showIcons: false
    }
  },
  mounted() {
    this.startTyping()
  },
  methods: {
    async startTyping() {
      // 第一行
      this.currentLine = 0
      await this.typeText('text1', 'Hello Guys!')
      await this.delay(300)
      
      // 第二行
      this.currentLine = 1
      await this.typeText('text2', "I'm Meow Chen")
      await this.delay(300)
      
      // 第三行
      this.currentLine = 2
      await this.typeText('text3', "I'm currently working in software development. If you're interested, feel free to contact me. Thank you!")
      
      // 完成
      this.currentLine = -1
      this.showIcons = true
    },
    
    typeText(property, text) {
      return new Promise((resolve) => {
        let i = 0
        const timer = setInterval(() => {
          if (i < text.length) {
            this[property] += text[i]
            i++
          } else {
            clearInterval(timer)
            resolve()
          }
        }, 50)
      })
    },
    
    delay(ms) {
      return new Promise(resolve => setTimeout(resolve, ms))
    },
    
    openEmail(email) {
      window.location.href = 'mailto:' + email
    },
    
    openUrl(url) {
      window.open(url, '_blank')
    }
  }
}
</script>

